.archive{
	&.post-type-archive-servicios{
		.masshead{
			background-image: url('../images/masshead--servicios.jpg');
			@include full-bg();
			.page-header{
				margin: 0 auto;
				padding: 0;
			}
		}
		.wrapper{
		@include make-row();
			.inner{
				@include make-lg-column(4);
				@include make-xs-column(6);
				padding: 0;
				.thumbnail{
					margin: 0;
					position: relative;
					border: 0 none;
					@include border-radius(0);
					padding: 0;
					@include focus-shadow();
					z-index: 0;
					@include fadeIn();
					&:hover{
						&:after{
							background-image: none;
							background: $red--google;
							@include fadeIn();
						}
					}
					.caption{
						z-index: 10;
						@include text-on-focus();
						position: absolute;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						a{
							@include text-on-focus();
						}
						h3, p, a{
							text-align: center;
						}

						h3{
							font-size: 16px;
						}
						p{
							font-size: 11px;
						}

						@media(min-width: $screen-lg-min){
							padding-top: 50px;
							h3{
								font-size: 24px;
							}
							p{
								display: none;
								visibility: hidden;
							}
							&:hover{
								h3{
									@include fadeInUp();
								}
								p{
									display: block;
									visibility: visible;
									@include fadeInUp();
								}
							}
						}
					}
				}
			}
		}
	}
	&.post-type-archive-producto{
		.masshead{
			background-image: url('../images/masshead--producto.jpg') !important;
			@include full-bg();
		}
		.main{
			figure.producto{
				@include make-xs-column(6);
			}
			@media(min-width: $screen-sm-min){
				figure.producto{
					@include make-xs-column(3);
					.thumbnail{
						background-color: $brown--lighter;
						padding: 0;
						border: 0 none;
						@include border-radius(0px);
						a{
							display: block;
							img{}
						}
						figcaption{
							background-color: $white--smoke;
							padding: 0;
							h3{
								@include opensans();
								font-size: 100%;
								margin: 0;
								padding: 5px 0;
								a{
									color: $gray;
								}

							}
							.cta__container{
								display: none;
								.cta__link{
									display: inline-block;
									font-size: 75%;
									padding: 0;
								}

							}
						}
						&:hover{
							figcaption{
								.cta__container{
									display: block;
									@include fadeInUp();
								}
							}
						}
					}
				}
			}
		}
	}
	.multimedia--1{
		@include make-row();
		margin: 0 0 30px;
		figure{
			@include gradient-vertical( $black, $gray--text--dark, 0%, 100% );
			padding: 30px 15px;
			.multimedia--1__video{
				@include make-sm-column(8);
			}
			.multimedia--1__caption{
				@include make-sm-column(4);
				.multimedia--1__caption__title{
					@include opensans();
					a{
						color: $gray--lighter;
						@include text-shadow();
					}
				}
				time{
					display: block;
					padding-bottom: 15px;
					@include opensans(semibold);
					color: $gray--light;
					@include text-shadow();
				}
				p{ 
					color: $gray--light;  
					@include text-shadow();
				}
			}
			@include clearfix();
		}
	}
	.multimedia--2{
		@include make-row();
		figure{
			@include make-xs-column(6);
			margin-bottom: 30px;
			.multimedia--2__icon{
				color: $black--light;
				position: absolute;
				top: 15px;
				left: 30px;
				@include opacity(.75);
				i{
					font-size: 24px;
				}
			}
			.multimedia--2__caption{
				position: absolute;
				bottom: 0;
				left: 15px;
				right: 15px;
				padding: 15px;
				opacity: .8;
				background: -webkit-gradient(linear,left top,left bottom,color-stop(0,rgba(0,0,0,.001)),color-stop(100%,#000));
				background: -webkit-linear-gradient(top,rgba(0,0,0,.001) 0,#000 100%);
				background: -o-linear-gradient(top,rgba(0,0,0,.001) 0,#000 100%);
				background: -webkit-gradient(linear,left top,left bottom,from(rgba(0,0,0,.001)),to(black));
				background: linear-gradient(to bottom,rgba(0,0,0,.001) 0,#000 100%);
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000', GradientType=0 );
				.multimedia--2__caption__title{
					margin: 0;
					font-size: 14px;
					@include opensans(bold);
					a{
						color: $white;
						@include text-shadow();
					}
				}
				time{
					@include opensans();
					font-size: 11px;
					color: $white; 
					@include text-shadow();
				}
			}
		}
	}
	&.term-post-format-gallery{
		.multimedia--2{
			figure{
				.thumbnail{
					margin-bottom: 0;
				}
			}
		}
	}
}

.single-servicios{
	.masshead{
		background-image: url('../images/masshead--servicios.jpg');
		@include full-bg();
		.page-header{
			margin: 0 auto;
			padding: 0;
		}
	}
	&.videos-de-capacitacion{
		.main{
			.wrapper{
				@include make-row();
				.inner{
					@include make-sm-column(3);
					@include make-xs-column(12);
					.thumbnail{
						@inclide border-radius(0);
    					padding: 0;
    					img{
    						margin: 0;
    					}
    					.caption{
    						text-align: center;
    						a{
    							margin: 7px auto 3px;
    							padding: 0;
    							font-size: 14px; 
    							@include opensans(bold);
    						}
    					}
					}
				}
			}

		}
	}
}

.servicios__logo{
	margin: 0 auto;
	a{
		margin: 0 auto;
		@include img-replace( 420px, 300px);
		background-image: url('../images/serviagro__logo--sm.png');
	}
}

@media(min-width: $screen-sm-min){
	.archive{
		.multimedia--2{
			figure{
				@include make-sm-column(6);
			}
		}
	}
}

@media(min-width: $screen-lg-min){
	.archive{
		.multimedia--2{
			figure{
				@include make-lg-column(4);
			}
		}
	}
}